<template>
  <div>
    <!-- Loading -->
    <loading :active.sync="isLoading">
      <template slot="default">
        <div class="loading-image"></div>
      </template>
    </loading>

    <!-- Cart -->
    <div class="container pt-5 pb-4 cart">
      <div class="text-center">
        <h2 class="font-weight-bold mb-4 pb-2"></h2>
      </div>
      <div class="row d-flex justify-content-center" v-if="cart.carts.length !== 0">
        <div class="col-lg-10">
          <table class="table">
            <thead class="thead-light">
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </thead>
            <tbody>
              <tr v-for="item in cart.carts" :key="item.id">
                <td class="align-middle">
                  <button
                    type="button"
                    class="btn btn-outline-maple btn-sm"
                    @click="removeCartItem(item.id)"
                  >
                    <i class="far fa-trash-alt"></i>
                  </button>
                </td>
                <td class="align-middle">
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon"></div>
                </td>
                <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                <td
                  class="align-middle text-right"
                  :class="{'text-success': item.coupon}"
                >{{ item.final_total | currency }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="3" class="text-right"></td>
                <td class="text-right">{{ cart.total | currency }}</td>
              </tr>
              <!--  -->
              <tr v-if="cart.total !== cart.final_total">
                <td colspan="3" class="text-right text-success"></td>
                <td class="text-right text-success">{{ cart.final_total | currency }}</td>
              </tr>
            </tfoot>
          </table>
          <div class="input-group input-group-sm">
            <input type="text" class="form-control" placeholder="" v-model="coupon_code" />
            <div class="input-group-append">
              <button class="btn btn-outline-maple" type="button" @click="addCouponCode"></button>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="row d-flex justify-content-center">
        <div class="col-lg-10 empty-cart text-center"></div>
      </div>
    </div>

    <!--  -->
    <div class="container cart-form pb-5">
      <div class="text-center">
        <h2 class="font-weight-bold mb-4 pb-2"></h2>
      </div>
      <div class="row justify-content-center">
        <!--  -->
        <form class="col-lg-10" @submit.prevent="createOrder">
          <div class="form-group">
            <label for="useremail">
              <i class="fas fa-envelope"></i>
              Email (*)
            </label>
            <input
              type="email"
              class="form-control"
              name="email"
              id="useremail"
              v-model="form.user.email"
              placeholder=" Email"
              v-validate="'required|email'"
              :class="{'is-invalid': errors.has('email')}"
            />
            <!-- errors.first('email')  email  -->
            <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
          </div>

          <div class="form-group">
            <label for="username">
              <i class="fas fa-user"></i>
               (*)
            </label>
            <!-- is-invalid (Bootstrap 4 )  errors.has('name')  -->
            <input
              type="text"
              class="form-control"
              name="name"
              id="username"
              v-model="form.user.name"
              placeholder=""
              v-validate="'required'"
              :class="{'is-invalid': errors.has('name')}"
            />
            <!--  name  name  ()  (false -> true) -->
            <span class="text-danger" v-if="errors.has('name')"></span>
          </div>

          <div class="form-group">
            <label for="usertel">
              <i class="fas fa-phone"></i>
               (*)
            </label>
            <input
              type="tel"
              class="form-control"
              id="usertel"
              v-model="form.user.tel"
              placeholder=""
              name="userTel"
              v-validate="'required'"
              :class="{'is-invalid': errors.has('userTel')}"
            />
            <span class="text-danger" v-if="errors.has('userTel')"></span>
          </div>

          <div class="form-group">
            <label for="useraddress">
              <i class="fas fa-map-marker-alt"></i>
               (*)
            </label>
            <input
              type="text"
              class="form-control"
              name="address"
              id="useraddress"
              v-model="form.user.address"
              placeholder=""
              v-validate="'required'"
              :class="{'is-invalid': errors.has('address')}"
            />
            <span class="text-danger" v-if="errors.has('address')"></span>
          </div>

          <div class="form-group">
            <label for="comment">
              <i class="fas fa-comment-dots"></i>
              
            </label>
            <textarea
              name
              id="comment"
              class="form-control"
              cols="30"
              rows="10"
              v-model="form.message"
            ></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-maple btn-lg"></button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      products: [],
      product: {},
      status: {
        loadingItem: ""
      },
      isLoading: false,
      cart: {
        carts: []
      },
      coupon_code: "",
      form: {
        // 
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        },
        message: ""
      }
    };
  },
  methods: {
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      vm.$http.get(url).then(response => {
        vm.cart = response.data.data;
        console.log(response);
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      vm.$http.delete(url).then(response => {
        if (response.data.success) {
          vm.$bus.$emit("message:push", "", "success");
          vm.isLoading = false;
          vm.getCart(); // 
          vm.$bus.$emit("cartCreate:push");
        } else {
          vm.isLoading = false;
          vm.$bus.$emit("message:push", "Oops", "danger");
        }
      });
    },
    addCouponCode() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      vm.isLoading = true;
      const coupon = {
        code: vm.coupon_code // 
      };
      vm.$http.post(url, { data: coupon }).then(response => {
        console.log(response);
        console.log(response.data.message); // 
        if (response.data.success) {
          vm.isLoading = false;
          vm.$bus.$emit("message:push", "", "success");
          vm.getCart(); // 
          vm.$bus.$emit("cartCreate:push");
        } else if (response.data.message === "!") {
          vm.isLoading = false;
          this.$bus.$emit("message:push", "", "danger");
        } else if (response.data.message === "") {
          vm.isLoading = false;
          vm.$bus.$emit("message:push", "", "danger");
        }
      });
    },
    createOrder() {
      // 
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      // vm.isLoading = true;
      // 
      vm.$validator.validate().then(result => {
        if (result) {
          // do stuff if not valid.
          vm.$http.post(url, { data: order }).then(response => {
            console.log("", response);
            // 
            if (response.data.success) {
              vm.$router.push(`/customerCheckout/${response.data.orderId}`); // 
            }
            // this.getCart();
            vm.isLoading = false;
          });
        } else {
          console.log("");
        }
      });
    }
  },
  created() {
    const vm = this;
    vm.getCart();
    vm.$bus.$on("cartCreate:push", () => {
      vm.getCart();
    });
  }
};
</script>

<style scope lang="scss">
// Loading
.loading-image {
  background-image: url(../../assets/images/GIFs/KingSlime.gif);
  background-size: cover;
  width: 219px;
  height: 230px;
}

.cart {
  h2 {
    display: inline-block;
    font-weight: 600;
    border-bottom: 3px solid #c1170c;
  }
  .empty-cart {
    background-color: #ededed;
    border-radius: 10px;
    padding: 30px 0px;
  }
}

.cart-form {
  h2 {
    display: inline-block;
    font-weight: 600;
    border-bottom: 3px solid #c1170c;
  }
}
</style>



