<template>
  <div>
    <!-- Loading -->
    <loading :active.sync="isLoading">
      <template slot="default">
        <div class="loading-image"></div>
      </template>
    </loading>
    <!--  -->
    <div class="container py-3">
      <!-- Breadcrumb -->
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb bg-white">
          <li class="breadcrumb-item">
            <router-link to="/index" class="text-maple"></router-link>
          </li>
          <li class="breadcrumb-item">
            <router-link to="/category" class="text-maple"></router-link>
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            <span>{{ product.title }}</span>
          </li>
        </ol>
      </nav>

      <!-- Main Content -->
      <div class="row main">
        <div class="col-md-6 mb-4 mb-md-0">
          <div class="double-border p-3 p-sm-4 h-100">
            <figure class="mb-4 item-image" :style="{backgroundImage: `url(${product.imageUrl})`}"></figure>
            <h5 class="pt-4 text-maple border-top"></h5>
            <p>{{ product.content }}</p>
            <h5 class="pt-4 text-maple border-top"></h5>
            <p class="mb-0">{{ product.description }}</p>
          </div>
        </div>
        <div class="col-md-6">
          <div class="double-border p-3 p-sm-4">
            <!--  -->
            <span
              class="badge float-right badge-danger category-badge"
              v-if="product.category == ''"
            >{{ product.category }}</span>
            <span
              class="badge float-right badge-moderate category-badge"
              v-if="product.category == ''"
            >{{ product.category }}</span>
            <span
              class="badge float-right badge-maple category-badge"
              v-if="product.category=='' || product.category ==''"
            >{{ product.category }}</span>
            <span
              class="badge float-right badge-dark category-badge"
              v-if="product.category == ''"
            >{{ product.category }}</span>
            <!--  -->
            <h2 class="h3 product-title pb-4 border-bottom">
              <span class="pb-4">{{ product.title }}</span>
            </h2>
            <!--  -->
            <div class="h5 mt-4 mb-3" v-if="!product.price">NT{{ product.origin_price | currency }}</div>
            <div
              class="h6 text-secondary mt-4"
              v-if="product.price"
            > NT{{ product.origin_price | currency }}</div>
            <div
              class="h5 mb-3 text-maple sale-price"
              v-if="product.price"
            > NT{{ product.price | currency }}</div>
            <!--  -->
            <select name class="form-control my-3" v-model="product.num">
              <option value="0" selected disabled></option>
              <option :value="num" v-for="num in 10" :key="num"> {{num}} {{product.unit}}</option>
            </select>
            <p>
              
              <strong>{{ product.num * product.price | currency }}</strong>
              
            </p>
            <!--  -->
            <button
              type="button"
              class="btn btn-maple mb-4 w-100"
              @click="addToCart(product.id, product.num)"
            >
              <!-- <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i> -->
              
            </button>
            <h5 class="pt-4 text-maple border-top"></h5>
            <p></p>
            <div class="ship-info">
              <table class="table table-borderless">
                <tbody>
                  <tr>
                    <td class="font-weight-bold pr-0" width="96px"></td>
                    <td class="pl-0">
                      <span class="text-danger">6h</span>
                      <br />24h100
                    </td>
                  </tr>
                  <tr>
                    <td class="font-weight-bold pr-0" width="96px"></td>
                    <td class="pl-0">
                      
                      <br />Email
                    </td>
                  </tr>
                  <tr>
                    <td class="font-weight-bold pr-0" width="96px"></td>
                    <td
                      class="pl-0"
                    ></td>
                  </tr>
                  <tr>
                    <td class="font-weight-bold pr-0" width="96px"></td>
                    <td class="pl-0"></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from "../Pagination";

export default {
  data() {
    return {
      id: "",
      product: {
        num: 0
      },
      isLoading: false
    };
  },
  methods: {
    addToCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      const cart = {
        product_id: id,
        qty
      };
      vm.$http.post(url, { data: cart }).then(response => {
        console.log(response);
        if (response.data.message === "") {
          vm.$bus.$emit("message:push", "", "success");
          vm.$bus.$emit("cartCreate:push");
          vm.isLoading = false;
        } else if (response.data.message === "") {
          vm.isLoading = false;
          vm.$bus.$emit("message:push", "Oops", "danger");
        } else {
          vm.isLoading = false;
          vm.$bus.$emit("message:push", "Oops", "danger");
        }
      });
    }
  },
  created() {
    const id = this.$route.params.id; //  id
    // console.log(id);
    const vm = this;
    const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
    vm.isLoading = true;
    this.$http.get(url).then(response => {
      vm.product = response.data.product; //  id 
      vm.$set(vm.product, "num", 0); // 
      console.log(response);
      vm.isLoading = false;
    });
  }
};
</script>

<style scoped lang="scss">
.loading-image {
  background-image: url(../../assets/images/GIFs/KingSlime.gif);
  background-size: cover;
  width: 219px;
  height: 230px;
}
.main {
  .double-border {
    border-width: 4px;
    border-color: #939699;
    border-style: double;
    border-radius: 10px;
    // left side
    figure.item-image {
      height: 220px;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      @media (max-width: 576px) {
        height: 120px;
      }
    }
    // right side
    .category-badge {
      font-size: 14px;
      padding: 5px 8px;
    }
    .product-title span {
      font-weight: 600;
      letter-spacing: 3px;
      position: relative;
      &::after {
        background: #adb5bd;
        bottom: 3px;
        content: "";
        height: 2px;
        left: 0;
        position: absolute;
        right: 0;
      }
    }
    .sale-price {
      font-weight: 600;
      font-size: 22px;
    }
    select.form-control {
      padding: 10px;
      height: 100%;
      border: 1px solid #6c757d;
      font-size: 16px;
      letter-spacing: 2px;
    }
    select.form-control:hover {
      border: 1px solid #343a40;
      box-shadow: 0 1px 5px #ccc;
      transition: 0.3s all;
    }
    .ship-info {
      border: 3px solid #ccc;
      border-radius: 10px;
    }
  }
}
</style>



